.container {
	margin: auto;
	padding: 0 $base-gutter * 6;
	width: $width-comp;

	@media all and (max-width: 1000px) {
		width: 100%;
	}
}

.section {
	padding: $base-gutter * 12 0;
}

.section-header {
	font-size: 48px;
	font-weight: 600;
	line-height: 1.2;
	@media all and (max-width: 800px) {
		font-size: 32px;
	}
}

.section1 {
	background-color: $primary;
	background-image: url("../images/background_pattern.png");
	height: calc(90vh - #{$base-gutter * 10});
	max-height: 800px;
	overflow: hidden;
	padding: 0;
	position: relative;

	@media all and (max-width: 1000px), all and (max-height: 930px) {
		height: auto;
	}

	&:before {
		background: url("../images/image_top.png") left top no-repeat;
		content: '';
		height: 800px;
		left: 0;
		position: absolute;
		top: 0;
		width: 985px;
	}
	&:after {
		background: url("../images/image_bottom.png") left top no-repeat;
		content: '';
		height: 800px;
		right: 0;
		position: absolute;
		bottom: 0;
		width: 985px;
	}

	&__container {
		align-items: flex-start;
		background: url("../images/infogr.png") no-repeat right $base-gutter * 6 bottom $base-gutter * 10;
		display: flex;
		flex-direction: column;
		height: 100%;
		justify-content: space-between;
		padding: $base-gutter * 12 $base-gutter * 6;
		position: relative;
		z-index: 1;
		
		@media all and (max-width: 1000px), all and (max-height: 930px) {
			background: none;
		}
	}

	&__header {
		color: $white;
		font-weight: 400;
		margin-bottom: $base-gutter * 12;
		text-align: left;
		@media all and (max-width: 1000px), all and (max-height: 930px) {
			text-align: center;
		}
	}
	&__buttons {
		> *:not(:last-child) {
			margin-right: $base-gutter * 2;
		}
		@media all and (max-width: 1000px), all and (max-height: 930px) {
			margin: auto;
		}
	}
}

.section2 {
	background: $primary-dark url("../images/shadow.png") no-repeat top center;
	color: $white;
	padding-bottom: 0;
	@media all and (max-width: 1000px) {
		padding-bottom: $base-gutter;
	}
	
	&__container {
		> *:not(:last-child) {
			margin-bottom: $base-gutter * 8;
		}
	}
	&__header {
		text-align: center;
	}
}

.blocks {
	display: flex;
	@media all and (max-width: 1000px) {
		align-items: center;
		flex-direction: column;
	}
	> * {
		width: calc(100% - #{$base-gutter * 18});
		&:not(:last-child) {
			margin-right: $base-gutter * 6;
			
			@media all and (max-width: 1000px) {
				margin-right: 0;
			}
		}
	}
	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		@for $i from 1 through 4 {
			&:nth-child(#{$i}) {
				.blocks__image {
					background: url("../images/img-blocks#{$i}.png") no-repeat center center;
				}
			}
		}
	}
	&__image {
		height: $base-gutter * 25;
		width: 100%;
	}
	&__title {
		background: $primary-light;
		border-radius: $base-gutter;
		box-shadow: 4px 4px 15px 0 rgba(#170039, .5);
		color: $white;
		font-size: 16px;
		font-weight: 600;
		margin-bottom: $base-gutter * 4;
		padding: $base-gutter * 2 $base-gutter * 3;
		position: relative;
		&:before {
			background: $primary-light;
			content: '';
			display: block;
			height: $base-gutter * 4;
			left: calc(50% - 1px);
			position: absolute;
			top: 100%;
			width: 2px;
		}
	}
	&__text {
		border: 2px solid $primary-light;
		border-radius: $base-gutter;
		height: $base-gutter * 23;
		padding: $base-gutter * 3 $base-gutter * 1.5;
		text-align: center;
		
		@media all and (max-width: 1000px) {
			height: auto;
		}
	}
}

.example {
	background: url("../images/example.png") no-repeat;
	height: 400px;
	position: relative;
	width: 1104px;
	
	@media all and (max-width: 1000px) {
		display: none;
	}
	&:before {
		background: url("../images/example_shadow_left.png");
		content: '';
		height: 400px;
		right: 100%;
		position: absolute;
		top: 0;
		width: 46px;
	}
	&:after {
		background: url("../images/example_shadow_right.png");
		content: '';
		height: 400px;
		left: 100%;
		position: absolute;
		top: 0;
		width: 46px;
	}
}

.section3 {
	padding-bottom: 0;
	&__container {
		> * {
			margin-bottom: $base-gutter * 8;
		}
	}
	&__header {
		color: $header;
		text-align: center;
	}
	&__text {
		color: #00041d;
		font-size: 16px;
		font-weight: 600;
		line-height: 24px;
		text-align: center;
	}
}

.architecture {
	display: flex;
	@media all and (max-width: 1000px) {
		flex-direction: column;
	}
	&__body {
		display: flex;
		flex-wrap: wrap;
		
		.architecture__item {
			@for $i from 1 through 9 {
				&:nth-child(#{$i}) {
					.architecture__item-icon {
						background: $white url("../images/img-arch#{$i}.png") no-repeat center center;
					}
				}
			}
		}
	}
	&__item {
		border-radius: $base-gutter;
		background: #e5e9f5;
		color: $dim-dark;
		height: $base-gutter * 29;
		margin-bottom: $base-gutter * 6;
		padding: $base-gutter * 6 $base-gutter * 2 $base-gutter * 3;
		text-align: center;
		position: relative;
		width: calc((100% - #{$base-gutter * 18}) / 4);
		z-index: 1;
		&:not(:nth-child(4n)) {
			margin-right: $base-gutter * 6;
		}
	}
	&__item-icon {
		background: $white;
		border: 2px solid #cc80f3;
		border-radius: 50%;
		left: calc(50% - #{$base-gutter * 4});
		position: absolute;
		height: $base-gutter * 8;
		top: -$base-gutter * 4;
		width: $base-gutter * 8;
		z-index: 1;
	}
	&__item-title {
		color: $primary;
		font-size: 16px;
		font-weight: 600;
		margin-bottom: $base-gutter * 3;
	}
}

.footer {
	background: $dark;
	color: $dim;
	padding: $base-gutter * 12 0;

	&__container {
		align-items: stretch;
		display: flex;
		justify-content: space-between;
		@media all and (max-width: 1000px) {
			align-items: center;
			flex-direction: column-reverse;
			& > *:last-child {
				margin-bottom: $base-gutter * 6;
			}
		}
	}

	.copyright {
		display: flex;
		flex: 1 1 auto;
		flex-direction: column;
		font-size: 15px;
		justify-content: space-between;
		position: relative;

		&__picture {
			background: url("../images/logo-bottom.png") no-repeat -16px top;
			height: 320px;
			width: 320px;
			@media all and (max-width: 1000px) {
				display: none;
			}
		}
		&__text1 {
			font-size: 36px;
			font-weight: 300;
			left: $base-gutter * 26;
			position: absolute;
			top: $base-gutter * 2;
		}
		&__text2 {
			font-size: 28px;
			font-weight: 300;
			left: $base-gutter * 26;
			line-height: 1.2;
			position: absolute;
			top: $base-gutter * 25.5;
			width: $base-gutter * 12;
		}
		&__caption {
			align-items: center;
			display: flex;
			& > *:not(:last-child) {
				margin-right: $base-gutter * 3;
			}
		}
	}
}

.logo-small {
	background: url("../images/logo-small.png") no-repeat;
	height: 32px;
	width: 124px;
}
