.header {
	color: #fff;
	background: $dark;
	left: 0;
	position: fixed;
	top: 0;
	user-select: none;
	width: 100%;
	z-index: 32;
	
	&__container {
		align-items: center;
		display: flex;
		justify-content: space-between;
		margin: auto;
		max-width: $width-comp;
		min-height: $base-gutter * 10;
		padding: 0 $base-gutter * 6;
		
		& > *:not(:first-child) {
			margin-left: $base-gutter * 4;
		}
		
		@media all and (max-width: 800px) {
			padding: $base-gutter * 2 $base-gutter * 6;
		}
	}
	
	.logo {
		align-items: center;
		color: $white;
		cursor: pointer;
		display: flex;
		font-size: 16px;
		font-weight: 600;
		height: $base-gutter * 4;
		margin-right: auto;
		text-transform: uppercase;
		
		&__main {
			background: url("../images/logo-main.png") no-repeat left center;
			height: $base-gutter * 4;
			margin-right: $base-gutter * 2.5;
			width: 44px;
		}
		
		&__product {
			background: url("../images/logo-product.png") no-repeat left center;
			height: $base-gutter * 4;
			margin-right: $base-gutter * 1.5;
			width: $base-gutter * 4;
		}
	}
	
	.menu {
		cursor: pointer;
		display: flex;
		font-size: 16px;
		@media all and (max-width: 800px) {
			align-items: flex-end;
			background: $dark;
			display: none;
			flex-direction: column;
			position: absolute;
			right: $base-gutter * 3.5;
			top: $base-gutter * 10;
		}
		
		&.is-visible {
			display: flex;
		}
		
		&__item {
			border-bottom: 2px solid transparent;
			text-align: center;
			@media all and (max-width: 800px) {
				border: none;
				padding: $base-gutter / 2 $base-gutter;
				text-align: right;
				width: 100%;
			}
			
			&:not(:last-child) {
				margin-right: $base-gutter * 1.5;
				@media all and (max-width: 800px) {
					margin-right: 0;
				}
			}
			
			&.active {
				border-bottom: 2px solid $primary;
				@media all and (max-width: 800px) {
					background: $primary;;
					border: none;
				}
			}
			
			a,
			a:hover,
			a:focus {
				display: block;
				color: $white;
				padding: 6px $base-gutter * 1.5;
				text-decoration: none;
			}
		}
	}
	
	.login {
		color: $primary;
		font-size: 16px;
		margin-left: $base-gutter * 2.5;
	}
	
	.lang {
		border: 2px solid #253151;
		border-radius: $base-gutter * 2;
		color: $primary;
		display: flex;
		height: $base-gutter * 4;
		padding: 2px;
		position: relative;
		
		&__item {
			width: $base-gutter * 6;
			
			input {
				display: none;
				height: 0;
				width: 0;
			}
		}
		
		&__switch {
			align-items: center;
			border-radius: $base-gutter * 1.5;
			display: flex;
			font-weight: 600;
			justify-content: center;
			height: 100%;
			user-select: none;
		}
		
		input:checked + .lang__switch {
			background-color: $primary;
			color: $white;
		}
	}
	
	&__hamb {
		cursor: pointer;
		display: none;
		user-select: none;
		@media all and (max-width: 800px) {
			display: block;
		}
	}
	
	&__btn-wrap {
		overflow: hidden;
		transition: width .6s;
		width: 0;
		
		&.is-visible {
			width: 178px;
		}
		
		&:not(.is-visible) {
			margin-left: 0
		}
	}
	
	&__btn {
		white-space: nowrap;
	}
}
